html {
  font-family: Roboto, sans-serif;
}

body {
  --main-color: black;
  --active-color: #00acdc;
  text-transform: uppercase;
  background-color: #faebd7;
  margin: 0;
  padding: 0;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav, .nav-list {
  gap: 20px;
  list-style: none;
  display: flex;
}

.nav-link {
  color: var(--main-color);
  padding-top: 23px;
  padding-bottom: 23px;
  text-decoration: none;
  position: relative;
}

.nav-link:hover {
  color: var(--active-color);
}

.is-active {
  color: var(--active-color);
  position: relative;
}

.is-active:after {
  color: var(--active-color);
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

